import { FormControl } from '@angular/forms';
import { Component, OnInit, Input, Output } from '@angular/core';
import { MatRadioChange } from '@angular/material/radio';
import { Observable } from 'rxjs';
import { QuestionRadio } from '../models/question-radio.model';

@Component({
  selector: 'kkl-form-radio',
  template: (require('./form-radio.component.html').default || require('./form-radio.component.html')).length ? (require('./form-radio.component.html').default || require('./form-radio.component.html')) : '',
  styles: [(require('./form-radio.component.scss').default || require('./form-radio.component.scss')).length ? (require('./form-radio.component.scss').default || require('./form-radio.component.scss')) : '']
})
export class FormRadioComponent implements OnInit {

  @Input() public question: QuestionRadio
  @Input() public control: FormControl
  @Input() public dark: boolean

  @Output() value: Observable<any>

  constructor() { }

  ngOnInit(): void {

    this.value = this.control.valueChanges

  }

  public handleChange(radio: MatRadioChange) {
    this.control.setValue(radio.value)

  }
}
