import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { StepModel, StepType } from './step.model';
import { Observable } from 'rxjs';
import { Classes } from '../../utilities/directives/classes.directive';

@Component({
  selector: 'app-step',
  template: (require('./step.component.html').default || require('./step.component.html')).length ? (require('./step.component.html').default || require('./step.component.html')) : '',
  styles: [(require('./step.component.scss').default || require('./step.component.scss')).length ? (require('./step.component.scss').default || require('./step.component.scss')) : ''],
})
export class StepComponent implements OnInit {
  @Input() public step: StepModel;

  public type: StepType;
  public active$: Observable<boolean>;

  public wizardClasses: Classes = {
    color: 'text',
    fontWeight: 500,
    fontSize: 1.1,
  };
  public activeWizardClasses: Classes = {
    ...this.wizardClasses,
    color: 'paper',
    fontWeight: 600,
  };

  @Output() onStepChange: EventEmitter<StepModel> = new EventEmitter();

  constructor() {}

  ngOnInit(): void {
    this.type = this.step.type;
    if (this.step) {
      this.active$ = this.step.getActiveObs();
    }
  }

  public onStepClick(): void {
    if (!this.step.isActive) {
      this.onStepChange.emit(this.step);
    }
  }
}
